.post {
    display: grid;
    grid-template-columns: 50px 1fr 100px;
    gap: 10px;
    margin-top: 5px;
    border: 1px solid #bdbdbd;
    border-radius: 5px;
    padding: 5px;
}

.avatar {
    composes: avatar from "../../../../App.module.css";
}

.post_avatar {
    height: 50px;
    width: 50px;
}

.image {
    composes: image from "../../../../App.module.css";
}

.text {
    display: flex;
    min-width: 100%;
}

.text div {
    min-width: 100%;
    word-wrap: break-word;
}